import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { RestServiceProvider } from '../../providers/rest-service/rest-service';
import {AuthService} from "../../provider/auth-service";
import { TextToSpeech } from "@ionic-native/text-to-speech";

/**
 * Generated class for the PurchasedPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-purchased',
  templateUrl: 'purchased.html',
})
export class PurchasedPage {

  username: any;
  email: any;
  products: any;

  getPurchasedProducts() {
    this.products = null;
    this.restProvider.getUserInfo(this.email).then(data => {
      this.restProvider.getPurchasedProducts(data["iduser"])
        .then(data => {
          this.products = data;
          console.log(data);
        });
    });
  }
  doRefresh(refresher) {

    this.getPurchasedProducts();
    setTimeout(() => {
      refresher.complete();
    }, 500);
  }
  doRefresh2() {

    this.getPurchasedProducts();
    setTimeout(() => {
    }, 500);
  }
  deleteFunct(item, refresher)
  {
    console.log(item);
    this.restProvider.deleteProduct(item);
    this.doRefresh2();

  }

  reinsertFunct(item, refresher)
  {
    console.log(item);
    this.restProvider.reinsertProduct(item);
    this.doRefresh2();

  }
  speech(itemName)
  {
    this.tts.speak(itemName)
      .then(() => console.log('Success'))
      .catch((reason: any) => console.log(reason));
  }

  constructor(public navCtrl: NavController, public navParams: NavParams, public restProvider: RestServiceProvider, private auth: AuthService, public tts: TextToSpeech) {

    let info = this.auth.getUserInfo();
    this.username = info['name'];
    this.email = info['email'];
  }
  ionViewWillEnter() {
    this.getPurchasedProducts();
    this.doRefresh2();
  }
  ionViewDidLoad() {
    this.getPurchasedProducts();
  }

}



